body { 
  background-image:linear-gradient(to right, rgba(0, 151.59753173589706, 184.87500607967377, 1),rgba(0.2833355963230133, 55.81101380288601, 68.00000354647636, 1)) ;
}

table{
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 80%;
  text-align: center;
}

td,th{
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

tr:nth-child(even){background-color: #ff22;}

tr:hover {background-color: #ddd;}

th {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #04AA6D;
  color: white;
}

.main-div{
  margin-left:25%;
  padding:80px 16px;
}

/*nav bar*/

.header-nav-ul {
  list-style-type: none;
  margin-top: 8%;
  padding: 0;
  width: 20%;
  background-color: transparent;
  position: fixed;
  height: 80%;
  overflow: auto;
}
.header-nav-li{
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  background-color: whitesmoke;
  height: 10%;
  text-align: left;
  margin-top: 4%;
}

.header-nav-li a {
  display: block;
  color: rgba(0.2833355963230133, 55.81101380288601, 68.00000354647636, 1);
  font-weight: bold;
  padding: 8px 16px;
  text-decoration: none;
}

button{
  text-decoration: none;
}

.header-nav-li a.active {
  background-color: #04AA6D;
  color: white;
}

.header-nav-li a:hover:not(.active) {
  color: yellow;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  border-color: yellow;
}

.header-nav-li:hover:not(.active) {
  color: yellow;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  border-color: yellow;
}

